'use client'

import Image from 'next/image';

export default function ProfileSection() {
  return (
    <section className="py-16 container mx-auto px-4">
      <div className="flex flex-col lg:flex-row items-center gap-12">
        {/* Bio and Quotes */}
        <div className="flex-1 space-y-6">
          <h1 className="text-4xl font-bold text-sage mb-4 font-arabic">
            فيليكس اراش
          </h1>
          <h2 className="text-3xl font-english mb-6">
            Felix Arash
          </h2>
          <div className="prose prose-lg dark:prose-invert max-w-none">
            <p className="text-lg font-arabic leading-relaxed mb-4 text-peach/90">
              &rdquo;الموسيقى هي لغة الروح التي تتجاوز كل الحدود&ldquo;
            </p>
            <p className="text-base font-english leading-relaxed mb-6 text-coral/90">
              &quot;Music is the language of the soul that transcends all boundaries&quot;
            </p>
            <p className="text-base text-foreground/80">
              A passionate musician, photographer, and writer based in [Your Location]. 
              Blending middle-eastern traditions with modern artistry to create unique 
              cultural experiences through various forms of creative expression.
            </p>
          </div>
        </div>

        {/* Profile Image */}
        <div className="relative w-64 h-64 lg:w-80 lg:h-80">
          <Image
            src="/profile.jpg" // Add your profile image to public folder
            alt="Felix Arash"
            fill
            className="rounded-full object-cover shadow-2xl border-4 border-sage/20"
            priority
          />
        </div>
      </div>
    </section>
  );
}